<template>
    <div class="modal" >
        <div class="modal__block">
            <h2 class="modal__block-title">Отправить картинку</h2>
            <div class="modal__block-fields">
                <label>
                    <span>URL</span>
                    <input v-model="photo">
                </label>
                <label>
                    <span>Комментарий</span>
                    <textarea class="win" v-model="text"></textarea>
                </label>
            </div>
            <div class="modal__block-btns" >
                <button class="modal__block-button del" @click="closeModal">
                    Отмена
                </button>
                <button class="modal__block-button ed" @click="sendImg">
                    ОТПРАВИТЬ
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                photo: '',
                text: '',   
            }
        },
        props: {
            
        },
         methods: {
             closeModal() {
                this.$emit('close')
            },
            sendImg() {
                this.$emit('send', {photo: this.photo, message: this.text})
                this.photo = "";
                this.closeModal()
            } 
        }, 
    }
</script>

