<template>
    <div class="window">
        <!-- HEADER -->
        <header class="window__header">
            <img src="@/assets/images/eugeniy.png" alt="">
            <div class="window__header-box">
                <h2 class="window__header-box-title">Евгений</h2>
                <p class="window__header-box-text">Онлайн</p>
            </div>
        </header>
        
        <!-- MAIN -->
        <div class="window__main second-user">
             <div class="window__main-info" 
                v-for="item in allMessages" 
                :key="item.id" 
                :class="{
                    sendItem: item.id == id,
                    getItem: item.id != id
                }">
                <span class="time">{{ item.time.hours }}:{{ item.time.min }}</span>
                <div class="window__main-info-msg" 
                    :class="{
                        sendMsg: item.id == id,
                        getMsg: item.id != id
                    }">  
                    <img v-if="item.photo" :src="item.photo" alt="">
                    <p>{{ item.message }}</p>
                </div>
                
            </div> 
            
        </div>
             
        <!-- FOOTER -->
        <div class="window__footer">
            <textarea class="ft" placeholder="Написать сообщение..." v-model="message2"></textarea>
            <button class="window__footer-btn" @click="isModalOpen = true" v-if="message2.length == 0">
                <img src="@/assets/images/camera.svg" alt="">
            </button>
            <button class="window__footer-btn" v-else  @click="sendMsg" >
                <img src="@/assets/images/send.svg" alt="">
            </button>
            <Modal
                v-if="isModalOpen" 
                @close="isModalOpen = false"
                @send="getPhoto"
            /> 
        </div>
        
    </div>
        
</template>

<script>

import Modal from '@/components/Modal.vue'

    export default {     
        data() {
             return {
                isModalOpen: false,
                id: 2,
                message2: '',
            } 
        },
        methods: {
            sendMsg() {
                let time = new Date()
                let min = time.getMinutes()
                let hours = time.getHours()
                this.$emit('userMsg', {id: this.id, message: this.message2, time: {min, hours}})
                this.message2= ''
            },
            getPhoto(obj) {
                let time = new Date();
                let min = time.getMinutes();
                let hours = time.getHours();
                obj.time= { min,hours }
                obj.id = this.id
                this.$emit('send', obj)
            },
        },
        props: {
             allMessages: {
                typeof: Array
            } 
        },
        components: { Modal }
    }
</script>